<template>
  <div class="json-tree">
    <JsonTreeNode ref="jsonTreeNode"
      :json-data="jsonData"
      :show-line="showLine"
      :indent="indent"
      :expand-deep="expandDeep">
      <template v-for="(_, slot) of $scopedSlots"
        v-slot:[slot]="scope">
        <slot :name="slot" v-bind="scope"></slot>
      </template>
    </JsonTreeNode>
  </div>
</template>

<script>
import JsonTreeNode from './JsonTreeNode'

export default {
  name: 'JsonTree',
  components: {
    JsonTreeNode
  },
  props: {
    jsonData: {
      required: true
    },
    // 
    showLine: {
      type: Boolean,
      default: true
    },
    // 
    //  string( '20px'),  20px
    //  number( 10),  10px
    indent: {
      type: [String, Number],
      default: '20px'
    },
    // 
    expandDeep: {
      type: Number,
      default: 2
    }
  },
  methods: {
    expandAll () {
      this.$refs.jsonTreeNode.expandNode()
    },
    collapseAll () {
      this.$refs.jsonTreeNode.collapseNode()
    }
  }
}
</script>

<style lang="scss" src="./json-tree.scss"></style>
